.cmn__wrapper {
  max-width: 1920px;
  min-width: 320px;
  padding: 0 12rem;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.cmn__block-title {
  font-family: "Montserrat-Medium";
  font-size: 3.6rem;
  line-height: 4.4rem;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 4rem;
  color: #000000;
}
.cmn__button {
  font-family: "Lobster-Regular";
  font-size: 3.6rem;
  line-height: 4.5rem;
  text-transform: capitalize;
  width: fit-content;
  padding: 0.5rem 3rem;
  border-radius: 5rem;
  box-shadow: 5px 5px 10px rgba(143, 31, 232, 0.2);
  color: #6C2F9B;
  background: rgba(255, 225, 79, 0.84);
}
.cmn__button--purple {
  background: rgba(143, 31, 232, 0.86);
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  color: rgba(255, 255, 255, 0.86);
}
.cmn__badge {
  width: 5rem;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: #f1_bold;
  font-size: 1.8rem;
  line-height: 2.2rem;
  text-transform: capitalize;
  border-radius: 50%;
  box-shadow: 5px 5px 10px rgba(143, 31, 232, 0.2);
  color: #ffffff;
}
.cmn__badge--new {
  background-color: #2CD93D;
}
.cmn__badge--sale {
  background-color: #E81F43;
}
.cmn__price {
  font-family: "Lobster-Regular";
  font-size: 2.4rem;
  line-height: 3rem;
  text-transform: capitalize;
  color: #6C2F9B;
}

