.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: #f7e9a2;
}
.header__top {
  background: rgba(243, 225, 130, 0.57);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
}
.header__main {
  padding: 0.5rem 0;
  background: linear-gradient(180deg, rgba(214, 193, 85, 0.09) 0%, rgba(165, 142, 24, 0.36) 100%);
  background-blend-mode: darken;
}
.header__main-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__nav {
  display: flex;
  align-items: center;
}
.header__nav-item:nth-child(1n+4) {
