.cats {
  margin-bottom: 3rem;
  padding-bottom: 2rem;
}
.cats__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 50rem;
  grid-gap: 2.5rem;
}
.cats__container li {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  box-shadow: 10px 10px 10px 5px rgba(143, 31, 232, 0.15);
}
.cats__container li a {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 5rem;
}
.cats__container li:first-of-type {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
}
.cats__container li:first-of-type a {
  position: relative;
}
.cats__container li:first-of-type a span {
  position: absolute;
  top: 15rem;
  left: -5rem;
  transform: rotate(-90deg);
}

