  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 5rem;
  height: 5rem;
  background: rgba(255, 225, 79, 0.84);
  box-shadow: 5px 5px 10px rgba(143, 31, 232, 0.2);
